html {
    scroll-behavior: smooth;
}

.App {
    text-align: center;
    overflow: hidden;
}

figure {
    margin: 0px;
}

.figure-img {
    margin-bottom: 0rem;
    line-height: 1;
}

a {
    color: var(--DarkGrey);
    font-family: "Prototype";
    font-size: larger;
}

a:hover {
    transition: all 0.2s;
    color: inherit;
    opacity: 0.5;
}

.nav-link {
    padding: 0.5rem 0.5rem;
}

#responsive-navbar-nav .nav-link {
    color: var(--DarkGrey);
}

#responsive-navbar-nav .nav-link {
    display: flex;
    justify-content: center;
    align-content: center;
    position: relative;
}

.UnderScoreLink {
    position: absolute;
}

.UnderScoreLinkActive {
    position: absolute;
    top: 2.6rem;
    height: 4px;
    background: var(--DarkGrey);
}

.UnderScore {
    transition: all 30ms ease-in-out;
    position: relative;
}

.white {
    background-color: white !important;
}

.grid-container-center {
    display: grid !important;
    min-height: 100%;
    /* Fallback for browsers do NOT support vh unit */
    min-height: 100vh;
    justify-content: center;
    align-content: center;
}

.carousel {
    width: 60vw;
}

.carousel-indicators {
    bottom: -2rem;
}

.carousel-indicators li {
    background-color: var(--DarkGrey);
}

.carousel-control-next-icon {
    position: relative;
    left: 4rem;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='0' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e");
}

.carousel-control-prev-icon {
    position: relative;
    right: 4rem;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='0' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e");
}

#infinite-carousel ul {
    margin: 0px;
}

.InfiniteCarouselFrame ul {
    margin: 0px !important;
}

.Cardcaro {
    position: relative;
    width: 60%;
    margin-left: auto;
    transform: translateX(-30%);
}

#infinite-carousel {
    width: 66%;
}

.card-text {
    white-space: pre-wrap;
}

.InfiniteCarouselArrowNext {
    left: auto;
    right: -30px !important;
}

.InfiniteCarouselArrowNext :hover {
    opacity: 0.5;
}

.InfiniteCarouselArrowPrev {
    left: -30px !important;
    right: auto;
}

.InfiniteCarouselArrow :hover {
    opacity: 0.5;
}

.caret-animate {
    animation-name: caret_updown;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes caret_updown {
    from {
        transform: translateY(0) rotate(180deg);
    }
    to {
        transform: translateY(20px) rotate(180deg);
    }
}

[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled),
button:not(:disabled) {
    cursor: pointer;
    outline-style: none;
    outline-width: 0ch;
    outline-color: var(--LightGrey);
}

.InfiniteCarouselArrowIcon {
    display: inline-block !important;
    padding: 10px !important;
    border: solid var(--DarkGrey) !important;
    border-width: 0 5px 5px 0 !important;
}

.figure>img {
    border-radius: 20px;
}

.figure>img:hover {
    transform: scale(0.9);
    transition: all ease;
}

@media only screen and (max-width: 680px) {
    .UnderScoreLinkActive {
        position: absolute;
        height: 4px;
        background: var(--DarkGrey);
    }
    #infinite-carousel ul {
        margin-top: 10%;
        margin-bottom: 1rem;
    }
}


/* width */

::-webkit-scrollbar {
    width: 4px;
}


/* Track */

::-webkit-scrollbar-track {
    background: #f1f1f1;
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: #888;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #555;
}

